:root {
  --image-comparison-slider-width: 90vh;
  --image-comparison-handle-width: 50px;
  --ui-fade-in-time: 0.25s;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Layout {
  position: relative;
  background-color: #282c34;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  flex-wrap: nowrap;
  color: white;
  width: 100%;
  height: 100%;
  max-height: 100%;
  max-width: 100%;
}

.image-gallery-slide img {
  width: 100%;
  height: auto;
  object-fit: cover;
  overflow: hidden;
  object-position: center center;
}

.fullscreen .image-gallery-slide img {
  max-height: 100vh;
}

.gallery-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  flex: 2;
}

#image-comparison-container {
  width: var(--image-comparison-slider-width);
  height: 100%;
  overflow: hidden;
  position: relative;
  flex-grow: 4;
}

#image-comparison-container .image-container {
  display: block;
  width: var(--image-comparison-slider-width);
  height: auto;
  max-height: 80vh;
  object-fit: cover;
}

#image-comparison-container .image-container canvas {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  
  image-rendering: -moz-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
}


#image-comparison-container .comparison-wrapper {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 50%;
  overflow: hidden;
  z-index: 1;
  object-fit: cover;
}

#image-comparison-container .comparison-wrapper .image-container {
  object-fit: cover;
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
}

#image-comparison-container .comparison-wrapper .image-container canvas {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  
  image-rendering: -moz-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
}

.image-container > .canvas-source {
  display: none;
  
  image-rendering: -moz-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
}

.image-container > .canvas-source * {
  display: none;
  
  image-rendering: -moz-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
}

#image-comparison-container .label {
  position: absolute;
  z-index: 3;
  color: #ffffff;
  border-radius: 0.25rem;
  padding: 0.5rem 0.75rem;
  font-size: 0.85rem;
  text-align: center;
  letter-spacing: 1px;
  user-select: none;
  opacity: 0;
  transition: var(--ui-fade-in-time) cubic-bezier(0.68, 0.26, 0.58, 1.22);
}

#image-comparison-container:hover .label {
  opacity: 1;
}

#image-comparison-container .label-image1 {
  background-color: #1f377a80;
  left: 1rem;
  bottom: 1rem;
}

#image-comparison-container .label-image2 {
  background-color: #d11f0054;
  top: 1rem;
  right: 1rem;
}

#image-comparison-container .reset-button {
  background-color: forestgreen;
  bottom: 1rem;
  right: 1rem;
}

#image-comparison-container .handle {
  position: absolute;
  top: 0;
  left: calc(50% - var(--image-comparison-handle-width)/2);
  height: 100%;
  width: var(--image-comparison-handle-width);
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  pointer-events: none;
  opacity: 0;
}

#image-comparison-container:hover .handle {
  opacity: 1;
}

#image-comparison-container .handle-line {
  width: 2px;
  flex-grow: 1;
  background-color: white;
  pointer-events: none;
}

#image-comparison-container .handle-circle {
  width: var(--image-comparison-handle-width);
  height: var(--image-comparison-handle-width);
  color: white;
  border: 2px solid white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 100%;
  pointer-events: none;
}

.thumbnail-container {
  display: flex;
  flex-shrink: 2;
  flex-direction: column;
  justify-content: center;
  row-gap: 10px;
  max-width: 8vw;
  max-height: 100%;
}

.thumbnail-container .thumbnail-entry {
  max-width: 20vh;
  min-width: 48px;
  min-height: 27px;
  flex: 1;
}

.thumbnail-container .thumbnail-entry img {
  width: 100%;
  height: 100%;
}

.thumbnail-unselected {
  border: 2px solid lightslategrey;
}

.thumbnail-forbidden {
  color: rgba(0, 0, 0, 0.5);
  border: 2px solid darkred;
}

.thumbnail-highlight {
  border: 2px solid mediumblue;
}

.thumbnail-container .thumbnail-entry .thumbnail-label {
  position: absolute;
  text-align: center;
  width: 100%;
  left: 0;
  bottom: 1rem;
  z-index: 10;
  font-size: calc(min(1vw, 1vh));
}

.sidebar {
  max-width: 25vw;
}

.context-menu {
  position: absolute;
  left: 0;
  top: 0;
  visibility: collapse;
}
